export default function fetchCountries(name, ref) {
  return fetch(
    `https://restcountries.com/v3.1/name/${name}?fields=name,capital,population,flags,languages`
  )
      .then((result) => {
        clearCountryList(ref);
      return result.json();
    })
      .then((data) => {

      const countries = data.map((dataItem) => {
        return {
          name: dataItem.name.official,
          capital: dataItem.capital[0],
          population: dataItem.population,
          flag: dataItem.flags.svg,
          languages: dataItem.languages,
        };
      });
      if (data.length > 10) {
        tooManyCountriesError();
      } else if (data.length > 1) {
        countries.map((country) => createListOfCountries(country, ref));
      } else {
        createCard(...countries, ref);
      }
    })
    .catch((error) => console.log(error));
}


function createCard({ name, capital, population, flag, languages }, ref) {

  let string = `<li class="country-card"><h2 class="country-card__title">${name}</h2><svg class="country-card__flag"><use href="${flag}"></use></svg><h3 class="country-card__subtitle">Capital:</h3><p class="country-card__text">${capital}</p><h3 class="country-card__subtitle">Population:</h3><p class="country-card__text">${population}</p><h3 class="country-card__subtitle">Languages:</h3><p class="country-card__text">${Object.values(
    languages
  ).join(", ")}</p></li> `;
  ref.insertAdjacentHTML("afterbegin", string);
}

function createListOfCountries({ name, flag }, ref) {
  let string = `<li class="country-list__item"><svg class="country-list__flag">
  <use href="${flag}"></use>
</svg>${name}</li> `;
  ref.insertAdjacentHTML("afterbegin", string);
}

function tooManyCountriesError() {
  window.alert("Слишком много стран");
}

function clearCountryList(ref) {
  ref.innerHTML = "";
}